import React, { useState } from 'react';
import { Container, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button, Typography, ButtonGroup, CircularProgress, Divider } from '@mui/material';
import { makeStyles } from '@mui/styles';
import { TextField, Card, CardContent, CardActions, Grid,Tooltip as Tt } from '@mui/material';
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Line } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js"
ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);
const toth_img = "https://github.com/dydtkddl/ICPS_ICPH/assets/114085307/e73fa8cf-09ae-4cdd-9443-b27049e1085b"
const useStyles = makeStyles({
  tableContainer: {
    marginTop: '20px',
  },
  tableCell: {
    borderBottom: '1px solid rgba(224, 224, 224, 1)',
    padding: '8px',
    fontSize: '19px',
    backgroundColor: '#4CAF50', //  
    color: 'white', //  
    textAlign :"center"
  },
  inputCell: {
    paddingLeft : "10px",
    border: 'none',
    boxSizing: 'border-box',
    fontSize: '15px',
    fontFamily: 'Arial, sans-serif',
    backgroundColor: '#A9DFBF', //  
  },
  inputoutercell : {
    padding : "3px !important",
    border :  '1px solid rgba(224, 224, 224, 1)',
    boxSizing: 'border-box',
    backgroundColor: '#A9DFBF',
  },
  card: {
    marginBottom: '20px',
    textAlign: 'center',
    backgroundColor: '#f5f5f5',
    borderRadius: '10px',
  },
  cardContent: {
    paddingBottom: '0px',
    paddingRight : '40px',
    paddingLeft : '40px'
  },
  parameterCell: {
    fontWeight: 'bold',
  },
  tableHeader: {
    backgroundColor: '#1976d2',
    color: '#fff',
  },
  tableRow: {
    '&:nth-of-type(odd)': {
      backgroundColor: '#e3f2fd',
    },
  },
  ResultTitle : {
    fontFamily: 'Carter One, sans-serif',
  }
});

const DataTable = () => {
  const classes = useStyles();
  const [tables, setTables] = useState([{ id: 1, name: 'Table 1', data: [['', ''], ['', '']], initialValues: { a: '', b: '', c: '' } }]);
  const [activeTable, setActiveTable] = useState(1);
  const [tableNames, setTableNames] = useState({1 : "Table 1"});
  const [submitState, setSubmitState] = useState(0);
  const [result, setResult] = useState([]);
  const [chartdata, setChartData] = useState({});

  const calculateMSE = (data) => {
    return Math.random() * 100;
  };

  const handlePaste = (event, tableId) => {
    event.preventDefault();
    const clipboardData = event.clipboardData || window.clipboardData;
    const pastedData = clipboardData.getData('text');
    const rows = pastedData.split('\n').map(row => row.split('\t'));

    const updatedTables = tables.map(table => {
      if (table.id === tableId) {
        return { ...table, data: rows };
      }
      return table;
    });
    setTables(updatedTables);
  };

  const addData = () => {
    const newTableId = tables.length + 1;
    setTables([...tables, { id: newTableId, name: `Table ${newTableId}`, data: [['', ''], ['', '']], initialValues: { a: '', b: '', c: '' } }]);
    setActiveTable(newTableId);
  };

  const handleNewButtonChange = (event, tableId, parameter) => {
    const updatedTables = tables.map(table => {
      if (table.id === tableId) {
        return { ...table, initialValues: { ...table.initialValues, [parameter]: event.target.value } };
      }
      return table;
    });
    const newTN = {...tableNames, [tableId] : event.target.value}
    setTableNames(newTN)
    setTables(updatedTables);
  };

  const saveTableNames = (tableId) => {
    const updatedTables = tables.map(table => {
      if (table.id === tableId) {
        return { ...table, name: tableNames[tableId] };
      }
      return table;
    });
 
    setTables(updatedTables);
  };

  const sendDataToServer = async (jsonData) => {
    console.log(jsonData);
    try {
      const response = await fetch('http://127.0.0.1:8000/submit_datas/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(jsonData),
      });

      if (!response.ok) {
        throw new Error('Failed to send data to the server');
      }

      const responseData = await response.json();
      console.log('Server response:', responseData);
      setResult(responseData);
      responseData.forEach((res) => {
        let options = {
          responsive: true,
          plugins: {
            legend: {
              position: "top",
            },
          
          },
        };
        let data = {
          labels: res.x_data,
          datasets: [
            {
              label: "original",
              data: res.original_data,
              borderColor: "rgb(255, 99, 132)",
              backgroundColor: "rgba(255, 99, 132, 0.5)",
            },
            {
              label: "fitting",
              data: res.fitting_data,
              borderColor: "rgb(0, 99, 132)",
              backgroundColor: "rgba(0, 99, 132, 0.5)",
            },
          ],
        };
        let rid = res.id;

        setChartData((chartdata) => ({
          ...chartdata,
          [rid]: { options: options, data: data },
        }));
      });
    } catch (error) {
      console.error('Error sending data to the server:', error);
    }
  };

  const handleSubmit = () => {
    setSubmitState(1);
    sendDataToServer(tables);
    setTimeout(() => {
      setSubmitState(2);
    }, 500);
  };

  const reloadPage = () => {
    window.location.reload();
  };

  return (
    <Container>
      {submitState === 0 && (
        <div>
          <Button variant="contained" onClick={addData} sx = {{paddingX : "40px"}}>Add Data Table</Button>
          <br></br>
          <br></br>
          <ButtonGroup>
            {tables.map(table => (
              <Button key={table.id} onClick={() => setActiveTable(table.id)}>{table.name}</Button>
            ))}
          </ButtonGroup>
          <Divider style={{ margin: '30px 0' }} />

          {tables.map(table => (
            <div key={table.id} style={{ display: table.id === activeTable ? 'block' : 'none' }}>
              <TextField
                label="Set Table Name"
                value={tableNames[table.id]}
                onChange={(event) => handleNewButtonChange(event, table.id, 'name')}
                style={{ marginBottom: '10px', fontFamily: 'Arial, sans-serif' }}
              />
              <Button variant="contained" onClick={() => saveTableNames(table.id)}>Save</Button>
              <TableContainer
                component={Paper}
                className={classes.tableContainer}
              >
                <Table>
                  <TableHead>
                    <TableRow className={classes.tableHeader}>
                      <TableCell className={classes.tableCell}>Pressure</TableCell>
                      <TableCell className={classes.tableCell}>N_a</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {table.data.map((row, rowIndex) => (
                      <TableRow key={rowIndex} className={classes.tableRow}>
                        {row.map((cell, cellIndex) => (
                          <TableCell key={cellIndex} className={classes.inputoutercell}>
                            <input
                              type="text"
                              className={classes.inputCell}
                              value={cell}
                              onChange={(event) => {
                                const updatedTables = tables.map(tbl => {
                                  if (tbl.id === table.id) {
                                    const updatedData = [...table.data];
                                    updatedData[rowIndex][cellIndex] = event.target.value;
                                    return { ...tbl, data: updatedData };
                                  }
                                  return tbl;
                                });
                                setTables(updatedTables);
                              }}
                              onPaste={(event) => handlePaste(event, table.id)}
                            />
                          </TableCell>
                        ))}
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </TableContainer>
              <br /><br />
              <Button fullWidth variant="contained" onClick={handleSubmit}>Submit</Button>
            </div>
          ))}
        </div>
      )}
      {submitState === 1 && (
        <div>
          <CircularProgress />
          <Typography variant="body1">Loading...</Typography>
        </div>
      )}
      {submitState === 2 && (
        <div>
          
          <Typography  variant="h4" style={{ marginBottom: '20px' }}>Optimize Result !!</Typography>
          <img src={toth_img} alt="Your Image" />
          <Carousel showArrows={true} renderArrowPrev={(onClickHandler, hasPrev, label) =>
    hasPrev && (
      <button
        type="button"
        onClick={onClickHandler}
        title={label}
        style={{
          position: 'absolute',
          zIndex: 2,
          left: 10,
          top: '50%',
          transform: 'translateY(-50%)',
          backgroundColor: 'rgba(200, 200, 200, 0.5)',
          border: 'none',
          borderRadius: '10%',
          width: '30px',
          height: '90%',
          cursor: 'pointer',
          outline: 'none',
        }}
      >
        {'<'}
      </button>
    )
}
renderArrowNext={(onClickHandler, hasNext, label) =>
  hasNext && (
    <button
      type="button"
      onClick={onClickHandler}
      title={label}
      style={{
        position: 'absolute',
        zIndex: 2,
        right: 10,
        top: '50%',
        transform: 'translateY(-50%)',
        backgroundColor: 'rgba(200, 200, 200, 0.5)',
        border: '1px solid #fff',
        borderRadius: '10%',
        width: '30px',
        height: '90%',
        cursor: 'pointer',
        outline: 'none',
      }}
    >
      {'>'}
    </button>
  )
}>
            {result.map(res => (
              <div key={res.id} style={{ marginBottom: '20px' }}>
                <Card className={classes.card}>
                  <CardContent className={classes.cardContent}>
                    <Typography variant="h6">{res.table_name}</Typography>
                    <Divider style={{ margin: '10px 0' }} />
                    <Line options={chartdata[res.id].options} data={chartdata[res.id].data} />
                    <Tt title={<img src={toth_img} alt="Parameter Image" style={{ width: '100%', height: 'auto' }}  />} arrow>
                    <TableContainer style={{ marginTop: '20px' }}>
                      <Table>
                        <TableBody>
                          <TableRow>
                            <TableCell className={classes.parameterCell}>N_T Parameter:</TableCell>
                            <TableCell>{res["parameters"].a}</TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell className={classes.parameterCell}>b_T Parameter:</TableCell>
                            <TableCell>{res["parameters"].b}</TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell className={classes.parameterCell}>C_P_MAX Parameter:</TableCell>
                            <TableCell>{res["parameters"].c}</TableCell>
                          </TableRow>
                          <TableRow>
                            <TableCell className={classes.parameterCell}>MSE:</TableCell>
                            <TableCell>{res.mean_square_error}</TableCell>
                          </TableRow>
                        </TableBody>
                      </Table>
                    </TableContainer>
                    </Tt>
                  </CardContent>
                  <CardActions>
                    <Button size="small" color="primary">Edit</Button>
                  </CardActions>
                </Card>
              </div>
            ))}
          </Carousel>
        </div>
      )}
    </Container>
  );
};

export default DataTable;
